                        >  
                        </v-select>
                    </v-col>
                    <v-col cols="12" md="6">
                        <!-- <v-radio-group v-model="formData.etnik" required :rules="[v => !!v || 'is required']">
                            <a  class="text-left font-weight-bold"  style="font-size: 16px;">{{ $t('step3') }}</a>
                            <v-row>
