                <!-- <v-row class="align-center"><v-col><v-btn :style="{ background: 'linear-gradient(to left, #ff7e5f, #feb47b)' }" rounded @click="startCalculate()">{{ $t('calculate') }}</v-btn></v-col></v-row> -->
                 <v-row class="align-center text-center">
                    <v-col>
                        <v-btn class="text-white" style="background: #1c4e80" rounded @click="startCalculate()">{{ $t('calculate') }}</v-btn>
