<script>
import { mapState, mapActions } from 'vuex';
import ResultPage from '../result/index.vue'

import lookupData from '@/store/lookup'

import apiServicesFiles from "@/services/api";
const apiServices = new apiServicesFiles();


  export default {
    data: () => ({
      modal: false,
      menu2: false,
      dob: null,
      dob2: null,
      formData: {
        tarikhLahir:'',
        jantina:'',
        etnik:'',
        umur:''
      }
    }),
    components:{
        ResultPage
    },
    mounted(){
        this.maxDate = this.getMaxDate();
        setTimeout(() => {
            const element = document.getElementById("headerDiv");
            if (element) {
                element.scrollIntoView({ behavior: 'smooth' });
            }
        }, 30); // Waits for 2000 milliseconds (2 seconds)
        
    },
    
    methods:{
        ...mapActions(['setSharedVariable']),
        async startCalculate(){
            var isValid = await this.$refs.form.validate();
 
            if(!isValid.valid || this.dob == null){
                return
            }

            this.formData.tarikhLahir = await this.formatDate(this.dob);
            this.formData.umur = await this.calculateAge(this.dob);
            
            if(this.formData.umur >= 80){

                const detailResult = await apiServices.getLookupAbove80(this.formData.jantina, "Malaysia", this.formData.etnik, this.formData.umur);

                this.formData.dataResult = detailResult;
                this.setSharedVariable(this.formData);
                this.saveResult(this.formData);
                setTimeout(() => {
                    this.scrollToSection('resultDiv');
                }, 15); // Waits for 2000 milliseconds (2 seconds)

            }else{

                const detailResult = await apiServices.getLookupBelow80(this.formData.jantina, "Malaysia", this.formData.etnik, this.formData.umur);

                this.formData.dataResult = detailResult;
                this.setSharedVariable(this.formData);
                this.formData.dataResult = detailResult;
                this.setSharedVariable(this.formData);
                this.saveResult(this.formData);
                setTimeout(() => {
                    this.scrollToSection('resultDiv');
                }, 15); // Waits for 2000 milliseconds (2 seconds)

                
            }

        },
        formatDate(date) {
            
            const day = String(date.getDate()).padStart(2, '0'); // Add leading zero if necessary
            const month = String(date.getMonth() + 1).padStart(2, '0'); // Months are 0-based, so add 1
            const year = date.getFullYear();
            this.formData.tahunLahir = year;
            this.dob2 = `${day}/${month}/${year}`;
            return `${day}/${month}/${year}`;
        },
        // Function to calculate age
        calculateAge(birthDate) {
            const today = new Date();
            let age = today.getFullYear() - birthDate.getFullYear();
            const month = today.getMonth();
            const day = today.getDate();

            // Check if the birthday has occurred this year
            if (month < birthDate.getMonth() || (month === birthDate.getMonth() && day < birthDate.getDate())) {
                age--;
            }

            return age;
        },
        getMinDate() {
            const today = new Date();
            const year = today.getFullYear() - 80; // 80 years ago
            return `${year}-01-01`;
        },
        getMaxDate() {
            const today = new Date();
            today.setDate(today.getDate() - 1); // Set to one day before today
            return today.toISOString().split('T')[0]; // Format to 'YYYY-MM-DD'
        },
        scrollToSection(sectionId) {
            const element = document.getElementById(sectionId);
            if (element) {
            console.log("bug2")
            element.scrollIntoView({ behavior: 'smooth' });
            }
        },
        changeDate(val){
            console.log(val)
        },
        reset(){
            //this.$refs.form.reset();
            // const div = document.getElementById('resultDiv');
            // const div2 = document.getElementById('stateResult');
            this.$router.go(0);
            window.scrollTo({ top: 0, behavior: 'smooth' });
        },
        async saveResult(data){
            await apiServices.saveResult(data);
        }
    },
    watch:{
        dob(old){
            //console.log("changes")
            this.formatDate(this.dob);
        }
    }
  }
</script>