<template>
    <v-img
        class="responsive-image"
        :src="require('@/assets/banner/header_bm.png')"
        v-if="currentLanguage == 'ms'"
    ></v-img>
    <v-img
        class="responsive-image"
        :src="require('@/assets/banner/header_bi.png')"
        v-else
    ></v-img>
</template>

<script>

import { useI18n } from 'vue-i18n'
export default {
 
  data() {
    return {
      currentLanguage: 'ms'
      
    }
  },
  mounted(){
     
  },
  watch: {
    '$i18n.locale'(newLang, oldLang) {
      //console.log(`Language changed from ${oldLang} to ${newLang}`)
      this.currentLanguage = newLang;
    }
  }
}
</script>


<style scoped>
.responsive-image {
  width: 100%;
  height: 100%;
}

</style>