<template>
    <div>
      <v-tabs
        v-model="tab"
        align-tabs="center"
        height="50"
        @click="toggleTabWindow"
      >
        <v-tab
          v-for="(item, index) in tabs"
          :key="item.value"
          :class="['rounded-xl', { active: tab === item.value },{ 'text-black': tab === item.value, 'text-white': tab !== item.value }]"
          :style="{ backgroundColor: tab === item.value ? item.color : '#1c4e80' }"
          :prepend-icon="item.icon"
          :text="$t(item.text)"
          :value="item.value"
        ></v-tab>
      </v-tabs>
  
      <v-tabs-window v-model="tab" v-if="showTabWindow">
        <v-tabs-window-item v-for="(item, index) in tabs" :key="item.value" :value="item.value" class="pa-4 ml-2 mr-2" style="text-align: justify; text-justify: inter-word;">
          
          <template v-if="item.value == 'tab-1'">
            <h4><strong>{{ $t('tab1title') }}</strong></h4>

            <!-- <p>{{ $t('tab1Para1') }}</p> -->
             <div v-html="purify(apiData.pengenalan)"></div>

          </template>

          <template v-if="item.value == 'tab-2'">
            <h4><strong>{{ $t('tab2title') }}</strong></h4>

            <!-- <ul>
              <li><p>{{ $t('tab2Para1') }}</p></li>
              <li><p>{{ $t('tab2Para2') }}</p></li>
            </ul>
             -->
            <div v-html="purify(apiData.kegunaan)"></div>
          </template>

          <template v-if="item.value == 'tab-3'">
            <h4><strong>{{ $t('tab3title') }}</strong></h4>

            <!-- <href>{{ $t('tab3Para1') }}</href> -->
             <div v-html="purify(apiData.penerbitan)"></div>
            
          </template>

          <template v-if="item.value == 'tab-4'">
            <h4><strong>{{ $t('tab4title') }}</strong></h4>
            <!-- <p>{{ $t('tab4Para1') }}</p> -->
             <div v-html="purify(apiData.penafian)"></div>
            
          </template>
        </v-tabs-window-item>
      </v-tabs-window>
    </div>
  </template>
  
  <script>
  import { marked } from 'marked'
  import DOMPurify from 'dompurify'

  import apiServicesFiles from "@/services/api";
  const apiServices = new apiServicesFiles();

  export default {
    data() {
      return {
        tab: null,
        showTabWindow: false,
        currentLanguage: 'ms',
        apiData:'',
        tabs: [
          { value: 'tab-1', icon: 'mdi-information-variant-circle-outline', text:  'menuTitle1' , desc: 'Introduction go here', color: '#00FFFF' },
          { value: 'tab-2', icon: 'mdi-lightbulb-on-outline', text: 'menuTitle2', desc: 'Uses go here', color: '#00FFFF' },
          { value: 'tab-3', icon: 'mdi-search-web', text: 'menuTitle3', desc: 'Other sources go here', color: '#00FFFF' },
          { value: 'tab-4', icon: 'mdi-hand-back-right-outline', text: 'menuTitle4', desc: 'Disclaimer go here', color: '#00FFFF' },
        ],
      };
    },
    mounted(){
      this.getData();
    },
    methods: {
      async getData(){
        const detailResult = await apiServices.getHeaderData(this.currentLanguage);
        this.apiData = detailResult;
      },
      toggleTabWindow() {
        if (!this.showTabWindow) {
          this.showTabWindow = true;
        }
      },
      purify(data){
        return DOMPurify.sanitize(marked(data))
      }
    },
    watch: {
      '$i18n.locale'(newLang, oldLang) {
        //console.log(`Language changed from ${oldLang} to ${newLang}`)
        this.currentLanguage = newLang;
        this.getData();
      }
    }
  };
  </script>
  
  <style scoped>
  .active {
    font-weight: bold; /* Example for active tab styling */
  }
  </style>
  