<template>
  <div>
    <!-- <h1 class="text-center">{{ $t('welcome') }}</h1> -->
     <main-header id="headerDiv"></main-header>

    <v-row>
      <!-- <v-col align-self="start"></v-col>
      <v-col align-self="center"></v-col> -->
      <v-spacer></v-spacer>
      <v-spacer></v-spacer>
      <v-spacer></v-spacer>
      <v-spacer></v-spacer>
      <v-col align-self="end">
        <v-btn
          color="primary"
          :class="{ 'active-btn': currentLanguage === 'ms' }"
          rounded
          size="small"
          @click="changeLanguage('ms')"
        >
          My
        </v-btn>
        <v-btn
          color="primary"
          :class="{ 'active-btn': currentLanguage === 'en' }"
          rounded
          size="small"
          @click="changeLanguage('en')"
        >
          En
        </v-btn>
      </v-col>
    </v-row>
  </div>
</template>

<script setup>
import { useI18n } from 'vue-i18n'
import { computed } from 'vue'
import MainHeader from "@/views/components/header/index.vue";

const { locale } = useI18n()

const changeLanguage = (lang) => {
  locale.value = lang
}

const currentLanguage = computed(() => locale.value)
</script>

<style scoped>
.active-btn {
  background-color: #a5cffa !important; /* Darker blue when active */
  color: white !important;
}
</style>
