<template>
  <v-container fluid>
    <v-row v-if="sharedVariable" id="resultDiv" justify="center">
      <v-col cols="12" md="10" lg="8">
        <v-card width="100%" class="pa-4">
          
          <!-- ================= MALAYSIA Section ================= -->
          <v-card-text>
            <v-card
              style="border: 2px solid black;"
              elevation="10"
              class="rounded-xl pa-4 text-center"
            >
              <v-card-title class="break-title">
                <strong>{{ $t('malaysiaTitle') }}</strong>
              </v-card-title>

              <p v-html="$t('result1', { umur: sharedVariable.umur })"></p>
              <p v-if="sharedVariable" v-html="$t('result2', { jangkahayat: sharedVariable.dataResult['life_expectancy'] })"></p>
              <p v-if="sharedVariable" v-html="$t('result3', { umur2: getLE(sharedVariable.umur, sharedVariable.dataResult['life_expectancy'], sharedVariable) })"></p>

              <div class="mt-4">{{ $t('step4') }}</div>
               <v-btn rounded size="small" color="#1c4e80" @click="generateQRCode">generate QR</v-btn>
               <div v-if="qrCodeValue" style="background-color: white;" class="mt-2 mb-2 ml-2 mr-2">
                  <canvas ref="qrCanvas" class="mt-2" style="height: 160px; width: 180px;"></canvas><br>
                  <v-btn color="warning" size="small" class="mt-2 mb-2" elevation="4" @click="downloadQRCode">DOWNLOAD</v-btn>
               </div>

              <v-row class="mt-2" align="center" justify="start">
                <v-col cols="12" sm="8">
                  <v-select
                    v-model="selectedState"
                    rounded
                    variant="outlined"
                    density="compact"
                    :items="listState"
                    :label="$t('labelState')"
                    hide-details
                  ></v-select>
                </v-col>

                <v-col cols="12" sm="4" class="d-flex align-center">
                  <v-btn
                    block
                    rounded
                    style="background: #1c4e80"
                    :disabled="!selectedState || !sharedVariable"
                    class="text-white"
                    @click="calculateByState"
                  >
                    {{ $t('calculate') }}
                  </v-btn>
                </v-col>
              </v-row>

            </v-card>
          </v-card-text>

          <!-- ================= Timeline Malaysia ================= -->
          <v-card-text class="pa-2">
            <v-timeline :direction="$vuetify.display.smAndDown ? 'vertical' : 'horizontal'" truncate-line="both">
              <v-timeline-item>
                <template v-slot:icon>
                  <v-avatar
                    image="https://static.vecteezy.com/system/resources/previews/013/740/512/non_2x/smiling-baby-swaddle-clipart-simple-cute-smile-baby-swaddle-in-blue-wrap-flat-illustration-happy-infant-baby-swaddling-cartoon-style-kids-baby-shower-newborn-and-nursery-decoration-concept-vector.jpg"
                  />
                </template>
                <template v-slot:opposite>{{ $t('birth') }}</template>
                <div>
                  <div class="text-h6"><strong>{{ tarikhLahir }}</strong></div>
                  <p>{{ $t('timeline1') }}</p>
                </div>
              </v-timeline-item>

              <v-timeline-item>
                <template v-slot:icon>
                  <v-avatar
                    image="https://thumbs.dreamstime.com/b/waving-hand-welcome-people-vector-illustration-young-man-woman-say-hello-standing-cartoon-character-set-hi-gesture-waving-343359774.jpg"
                  />
                </template>
                <template v-slot:opposite>
                  <div class="text-h6"><strong>{{ umur }} {{ $t('years') }}</strong></div>
                  <p>{{ $t('timeline2') }}</p>
                </template>
              </v-timeline-item>

              <v-timeline-item dot-color="#1c4e80" size="small">
    
                 <template v-slot:opposite>{{ jangkaUmur }} {{ $t('timeline3') }}</template>
                  <div>
                    <div class="text-h6"><strong>{{ jangkaUmur }} {{ $t('years') }}</strong></div>
                  </div>
                  
              </v-timeline-item>

              <v-timeline-item dot-color="grey" size="small">
                <template v-slot:opposite>{{ $t('timeline4') }}</template>
                <div>
                  <div class="text-h6"><strong>{{ hadUmur }} {{ $t('years') }}</strong></div>
                </div>
              </v-timeline-item>
            </v-timeline>
          </v-card-text>

          <!-- ================= Malaysia Chart ================= -->
          <area-chart />

          <!-- ================= State Section ================= -->
          <v-card
            v-if="stateLEDetails"
            class="rounded-xl mt-4 pa-4 text-center"
            style="border: 2px solid black;"
            elevation="10"
          >
            <v-card-title class="break-title"><strong>{{ $t('stateTitle') }} {{ selectedState }}</strong></v-card-title>
            <v-card-text>
              <p v-html="$t('result1', { umur: sharedVariable.umur })"></p>
              <p v-if="sharedVariable" v-html="$t('result2', { jangkahayat: stateLEDetails['life_expectancy'] })"></p>
              <p v-if="sharedVariable" v-html="$t('result3', { umur2: stateLEDetails.stateLE })"></p>
            </v-card-text>
          </v-card>

          <!-- ================= Timeline State ================= -->
          <v-card-text class="pa-2" v-if="stateLEDetails" id="stateResult">
            <v-timeline :direction="$vuetify.display.smAndDown ? 'vertical' : 'horizontal'" truncate-line="both">
              <v-timeline-item>
                <template v-slot:icon>
                  <v-avatar image="https://static.vecteezy.com/system/resources/previews/013/740/512/non_2x/smiling-baby-swaddle-clipart-simple-cute-smile-baby-swaddle-in-blue-wrap-flat-illustration-happy-infant-baby-swaddling-cartoon-style-kids-baby-shower-newborn-and-nursery-decoration-concept-vector.jpg" />
                </template>
                <template v-slot:opposite>{{ $t('birth') }}</template>
                <div>
                  <div class="text-h6"><strong>{{ tarikhLahir }}</strong></div>
                  <p>{{ $t('timeline1') }}</p>
                </div>
              </v-timeline-item>

              <v-timeline-item>
                <template v-slot:icon>
                  <v-avatar image="https://thumbs.dreamstime.com/b/waving-hand-welcome-people-vector-illustration-young-man-woman-say-hello-standing-cartoon-character-set-hi-gesture-waving-343359774.jpg" />
                </template>
                <template v-slot:opposite>
                  <div class="text-h6"><strong>{{ umur }} {{ $t('years') }}</strong></div>
                  <p>{{ $t('timeline2') }}</p>
                </template>
              </v-timeline-item>

              <v-timeline-item dot-color="#1c4e80" size="small">
                <!-- <div>
                  <div class="text-h6"><strong>{{ jangkaUmurState }}</strong></div> {{ $t('timeline3') }}
                </div> -->
                  <template v-slot:opposite>{{ jangkaUmurState }} {{ $t('timeline3') }}</template>
                  <div>
                    <div class="text-h6"><strong>{{ jangkaUmurState }} {{ $t('years') }}</strong></div>
                  </div>
              </v-timeline-item>

              <v-timeline-item dot-color="grey" size="small">
                <template v-slot:opposite>{{ $t('timeline4') }}</template>
                <div>
                  <div class="text-h6"><strong>{{ hadUmurState }} {{ $t('years') }}</strong></div>
                </div>
              </v-timeline-item>
            </v-timeline>
          </v-card-text>

          <!-- ================= State Chart ================= -->
          <state-area-chart v-if="sharedVariable.stateData" />
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
  import lookupData from '@/store/lookup'
  import lookupState from '@/store/lookupState'
  import { mapState } from 'vuex';

  import apiServicesFiles from "@/services/api";
  const apiServices = new apiServicesFiles();

  import AreaChart from "@/views/components/Chart/chart.vue"
  import StateAreaChart from "@/views/components/Chart/chart2.vue"

  import qrcode from 'qrcode-generator';
  import { Filesystem, Directory } from '@capacitor/filesystem';

  import { nextTick } from 'vue';

  export default {
    components:{
      AreaChart,
      StateAreaChart
    },
    data: () => ({
      showChart: false,
      selectedState:'',
      stateLEDetails:'',
      listState:'',
      leDetails:'',
      progressValue: 55,
      tarikhLahir:'',
      umur:'',
      jangkaUmur:'',
      hadUmur:'',
      jangkaUmurState:'',
      hadUmurState:'',
      icons: [
        'mdi-baby',
        'mdi-account-heart',
        'mdi-fire',
        'mdi-water',
      ],
      qrCodeValue: ''
    }),
    async mounted(){
      this.listState = [...new Set(lookupState.map(item => item.State))];
    },

    methods: {
      season (val) {
        return this.icons[val]
      },
      getLE(age, life, data){
        
        this.tarikhLahir = data.tarikhLahir;
        this.umur = age;
        this.jangkaUmur = data.dataResult["life_expectancy"];
        this.hadUmur = Number(age) + Number(this.jangkaUmur);


        this.leDetails = 'Your Life Expectancy '+ data.dataResult["life_expectancy"]+' years';
        return Number(age) + Number(life);
      },
      async calculateByState(){
      
        if(Number(this.sharedVariable.umur) <= 79){

          // const detailResult = await lookupState.find(obj => 
          //   obj.Gender === this.sharedVariable.jantina 
          //   && obj.State === this.selectedState
          //   && (Number(this.sharedVariable.umur) >= Number(obj["Min Age"]) && Number(this.sharedVariable.umur) <= Number(obj["Max Age"]))
          // );
          const detailResult = await apiServices.getLookupBelow80ByState(this.sharedVariable.jantina , this.selectedState, this.sharedVariable.umur);
          detailResult.stateLE = this.getLE(this.sharedVariable.umur, detailResult["life_expectancy"], this.sharedVariable);
          
          this.sharedVariable.stateData = detailResult;
          this.stateLEDetails = detailResult;
          this.jangkaUmurState = detailResult["life_expectancy"];
          this.hadUmurState = detailResult.stateLE;
          this.saveResult(this.sharedVariable);

        }else{

          // const detailResult = await lookupState.find(obj => 
          //   obj.Gender === this.sharedVariable.jantina 
          //   && obj.State === this.selectedState
          //   && (Number(this.sharedVariable.umur) >= 80)
          // );
          const detailResult = await apiServices.getLookupAbove80ByState(this.sharedVariable.jantina , this.selectedState, this.sharedVariable.umur);
          detailResult.stateLE = this.getLE(this.sharedVariable.umur, detailResult["life_expectancy"], this.sharedVariable);
          
          this.sharedVariable.stateData = detailResult;
          this.stateLEDetails = detailResult;
          this.jangkaUmurState = detailResult["life_expectancy"];
          this.hadUmurState = detailResult.stateLE;
          this.saveResult(this.sharedVariable);
        }

        setTimeout(() => {
          this.scrollToSection('stateResult');
        }, 15); // Waits for 2000 milliseconds (2 seconds)
        

        
        // detailResult.stateLE = this.getLE(this.sharedVariable.umur, detailResult["Life Expectancy"], this.sharedVariable);
        // this.stateLEDetails = detailResult;
        // this.jangkaUmurState = detailResult["Life Expectancy"];
        // this.hadUmurState = detailResult.stateLE;
      },
      updateProgress() {
        if (this.progressValue < 0) this.progressValue = 0;
        if (this.progressValue > 75) this.progressValue = 75;
      },
      scrollToSection(sectionId) {
            const element = document.getElementById(sectionId);
            if (element) {
              console.log("bug")
            element.scrollIntoView({ behavior: 'smooth' });
            }
      },
      async saveResult(data){
            await apiServices.saveResult(data);
      },
      generateQRCode() {
        console.log(this.sharedVariable)
        try {

          this.qrCodeValue = "Umur : "+ this.sharedVariable.umur +
          "\nJantina : "+ this.sharedVariable.jantina +
          "\nTahun Lahir : "+ this.sharedVariable.tahunLahir +
          "\nEtnik : "+ this.sharedVariable.etnik +
          "\nJangkaan Hayat (Umur) : "+ this.getLE(this.sharedVariable.umur, this.sharedVariable.dataResult['life_expectancy'], this.sharedVariable)+" tahun";

          const qr = qrcode(0, 'L'); // Version 0, Error correction level L
          qr.addData(this.qrCodeValue);
          qr.make();
          
          nextTick(() => {
            const canvas = this.$refs.qrCanvas;
            if (canvas) {
              const ctx = canvas.getContext('2d');
              canvas.width = qr.getModuleCount() * 10; // Set canvas size
              canvas.height = canvas.width;
              ctx.fillStyle = '#ffffff'; // Background color
              ctx.fillRect(0, 0, canvas.width, canvas.height);
              const cellSize = canvas.width / qr.getModuleCount();

              for (let r = 0; r < qr.getModuleCount(); r++) {
                for (let c = 0; c < qr.getModuleCount(); c++) {
                  if (qr.isDark(r, c)) {
                    ctx.fillStyle = '#000000'; // QR code color
                    ctx.fillRect(c * cellSize, r * cellSize, cellSize, cellSize);
                  }
                }
              }
            } else {
              this.errorMessage = 'Canvas is not available.';
            }
          });
          } catch (e) {
            console.log(e)
            alert('Invalid JSON data');
          }
      },
      async downloadQRCode() {
        const canvas = this.$refs.qrCanvas;
        const qrCodeData = canvas.toDataURL('image/png').split(',')[1]; // Get base64 data

        if (Capacitor.isNative) {
          // Mobile (Android/iOS)
          try {
            // Ensure the directory exists before writing the file
            await Filesystem.mkdir({
              path: 'DCIM/QRcode',
              directory: Directory.External,
              recursive: true, // Create the folder structure if it doesn't exist
            });

            
            await Filesystem.writeFile({
              path: 'DCIM/QRcode/LifeExpectancy.png',
              data: qrCodeData,
              directory: Directory.External,
            });
            console.log('QR Code saved successfully on device.');
          } catch (error) {
            console.log('Error saving QR Code:', error);
          }
        } else {
          // Web
          const link = document.createElement('a');
          link.href = canvas.toDataURL('image/png');
          link.download = 'LifeExpectancy.png';
          document.body.appendChild(link); // Append link to the DOM
          link.click(); // Trigger download
          document.body.removeChild(link); // Clean up
        }
      }

       
    },
    computed: {
      ...mapState(['sharedVariable'])
    },

  }
</script>

<style scoped>
@media (max-width: 600px) {
  .v-timeline {
    flex-direction: column !important;
  }

  .v-timeline-item {
    max-width: 100% !important;
  }
}
.custom-slider .v-slider-thumb__label {
  transform: scale(2.0); /* Adjust the label size */
  font-size: 18px; /* Change text size */
  min-width: 50px; /* Adjust width */
}

.custom-thumb-label {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.custom-text {
  font-size: 16px; /* Adjust text size */
  font-weight: bold;
}

.break-title {
  white-space: normal !important;
  word-break: break-word;
  text-align: center;
  justify-content: center;
}
</style>